html {
  /* font-size: 1vw; */
}

body {
  background-color: white;
}

#root {
  display: flex;
  flex-direction: column;
}

.App {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
}

.Intro {
  font-size: 40px;
  text-align: center;
}

.Intro > h1 > kbd {
  background-color: #6772e5;
  color: white;
}

.Intro > h1 {
  font-size: 80px;
  font-weight: 500;
}

.Intro > p {
  font-size: 76px;
  line-height: 1.5;
  margin: 1em auto;
  width: 75%;
}

.Sidebar {
  /* background-color: lightblue; */
  display: flex;
  flex-direction: column;
  padding-top: 40px;
}

.Sidebar .Menu {
  list-style-type: none;
  margin: 0;
  padding-left: 30px;
  position: sticky;
  top: 46px;
}

.Menu li {
  color: #6772e5;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 10px;
}

.Menu li.active {
  /* border-bottom: 1px solid #cfd7df; */
}

.Menu li.past {
  opacity: 0.4;
}

.Menu li.next {
  opacity: 0.7;
}

.Menu li.future {
  opacity: 0.7;
}

.Menu li:hover,
.Menu li.active {
  color: #32325d;
  cursor: pointer;
  opacity: 1;
}

.Content {
  flex: 1;
  margin: 40px;
}

.Rolodex dl {
  margin: 0;
}

.Rolodex dt {
  background-color: #32325d;
  border-bottom: 3px solid #6772e5;
  color: #f6f9fc;
  display: flex;
  justify-content: space-between;
  padding: 1em;
  position: sticky;
  top: -1px;
}

.Rolodex-Letter,
.Rolodex-Count {
  font-weight: 500;
  font-size: 40px;
}

.Rolodex dd {
  font-weight: 500;
  padding: 1em 0;
  margin: 0 20px;
}

.Rolodex dd + dd {
  border-top: 1px #eee solid;
}

.RolodexMini {
  background-color: #f6f9fc;
  flex: 1;
  padding: 20px;
}

.RolodexMini-Scroller {
  background-color: white;
  border: 1px solid #cfd7df;
  height: 70vh;
  overflow: scroll;
}

.RolodexMini > .Rolodex {
  background-color: white;
  margin: 20px;
}

.RolodexMini + .RolodexMini {
  margin-left: 20px;
}
