/*cssnext*/

/*

  Keep this file absolutely minimal.
  Only for styles that will be used on *absolutely every page.*

*/

/* Reset */

* {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
pre,
blockquote,
figure,
ul,
ol,
dl,
dd {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

/* Base styles */

body {
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;

  /* font-size: 62.5%; */
  font-size: 17px;
  font-family: Camphor, "Open Sans", "Segoe UI", sans-serif;
  font-weight: 400;
  font-style: normal;

  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Webfonts */

@font-face {
  font-family: Camphor;
  font-weight: 400;
  src: url("/camphor-400.woff2") format("woff2"),
    url("/camphor-400.woff") format("woff");
}

@font-face {
  font-family: Camphor;
  font-weight: 500;
  src: url("/camphor-500.woff2") format("woff2"),
    url("/camphor-500.woff") format("woff");
}
